* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.logo__img {
  display: block;
}

.nav {
  display: flex;
}

.nav__list {
  align-items: center;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #000;
  text-align: center;
  text-transform: uppercase;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
